/* Style the tab */
.tab {
  overflow: hidden;
  background: #91E5FA;
  margin-left: -15px;
  margin-right: -15px; }
  @media (min-width: 900px) {
    .tab {
      margin-left: 0;
      margin-right: 0px; } }
  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-weight: bold;
    padding: 7px;
    font-size: 1.1em;
    letter-spacing: 1px; }
    @media (min-width: 900px) {
      .tab button {
        font-size: 18px;
        padding: 14px 16px; } }
    .tab button:hover {
      background-color: #ddd; }
    .tab button.addLocation {
      float: right; }
    .tab button.active {
      background-color: #5FC0F5;
      color: white; }

/* Style the tab content */
.tabcontent {
